import React, { useState, useRef, useEffect, useCallback } from 'react';
import {
  Box,
  Container,
  TextField,
  IconButton,
  Typography,
  Paper,
  Avatar,
  CircularProgress,
  Stack,
  AppBar,
  Toolbar,
  Tooltip,
  Snackbar,
  Alert,
  Fade,
  Divider,
  useMediaQuery,
  useTheme,
  Badge,
  InputAdornment,
} from '@mui/material';
import {
  Send as SendIcon,
  SmartToy as BotIcon,
  Person as PersonIcon,
  MoreVert as MoreIcon,
  Mic as MicIcon,
  AttachFile as AttachFileIcon,
  MoreHoriz as TypingIcon,
  CheckCircle as CheckCircleIcon,
} from '@mui/icons-material';

const API_BASE_URL = 'https://agents.tericlab.com:8000';

// Message time formatter
const formatMessageTime = (timestamp) => {
  const date = new Date(timestamp);
  return date.toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' });
};

// Typing indicator component
const TypingIndicator = () => (
  <Box sx={{ display: 'flex', alignItems: 'center', gap: 0.5, px: 1 }}>
    <Box className="typing-dot" sx={{ width: 6, height: 6, borderRadius: '50%', bgcolor: 'primary.main', animation: 'typing-animation 1.4s infinite ease-in-out both', animationDelay: '0s' }} />
    <Box className="typing-dot" sx={{ width: 6, height: 6, borderRadius: '50%', bgcolor: 'primary.main', animation: 'typing-animation 1.4s infinite ease-in-out both', animationDelay: '0.2s' }} />
    <Box className="typing-dot" sx={{ width: 6, height: 6, borderRadius: '50%', bgcolor: 'primary.main', animation: 'typing-animation 1.4s infinite ease-in-out both', animationDelay: '0.4s' }} />
    <style>
      {`
      @keyframes typing-animation {
        0%, 80%, 100% { transform: scale(0.6); opacity: 0.6; }
        40% { transform: scale(1); opacity: 1; }
      }
      `}
    </style>
  </Box>
);

const Chats = ({ agentId, botName = 'AI Assistant', targetLang }) => {
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [isLoading, setLoading] = useState(false);
  const [agentInfo, setAgentInfo] = useState(null);
  const [isLoadingInfo, setIsLoadingInfo] = useState(true);
  const [snackbar, setSnackbar] = useState({ open: false, message: '', severity: 'success' });
  const [isStreaming, setIsStreaming] = useState(false);
  const [streamingText, setStreamingText] = useState('');
  const messagesEndRef = useRef(null);
  const inputRef = useRef(null);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const isTablet = useMediaQuery(theme.breakpoints.between('sm', 'md'));

  const scrollToBottom = useCallback(() => {
    messagesEndRef.current?.scrollIntoView({ behavior: 'smooth' });
  }, []);

  useEffect(() => {
    scrollToBottom();
  }, [messages, streamingText, scrollToBottom]);

  useEffect(() => {
    const fetchAgentInfo = async () => {
      try {
        const formData = new FormData();
        formData.append("agent_id", agentId);

        const response = await fetch(`${API_BASE_URL}/agent-info`, {
          method: 'POST',
          body: formData,
        });

        if (!response.ok) throw new Error('Failed to fetch agent info');

        const data = await response.json();
        setAgentInfo(data);
      } catch (error) {
        showSnackbar('Failed to load agent information', 'error');
      } finally {
        setIsLoadingInfo(false);
      }
    };

    fetchAgentInfo();
  }, [agentId]);

  // Welcome message effect
  useEffect(() => {
    if (!isLoadingInfo && agentInfo && messages.length === 0) {
      const welcomeMessage = {
        role: 'assistant',
        content: `Hello! I'm ${botName}. How can I assist you today?`,
        timestamp: new Date().toISOString()
      };
      setMessages([welcomeMessage]);
    }
  }, [isLoadingInfo, agentInfo, messages.length, botName]);

  const showSnackbar = (message, severity = 'success') => {
    setSnackbar({ open: true, message, severity });
  };

  const handleSnackbarClose = () => {
    setSnackbar((prev) => ({ ...prev, open: false }));
  };

  const streamResponse = async (response) => {
    setIsStreaming(true);
    let streamedText = '';
    const sentences = response.split(/(?<=[.!?])\s+/);
    
    for (let sentence of sentences) {
      const words = sentence.split(' ');
      for (let word of words) {
        streamedText += word + ' ';
        setStreamingText(streamedText);
        await new Promise(resolve => setTimeout(resolve, Math.random() * 40 + 10));
      }
      await new Promise(resolve => setTimeout(resolve, 150));
    }

    setIsStreaming(false);
    return streamedText.trim();
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!input.trim() || isLoading) return;

    const userMessage = { role: 'user', content: input.trim(), timestamp: new Date().toISOString() };
    setMessages((prev) => [...prev, userMessage]);
    setInput('');
    setLoading(true);

    try {
      const formData = new FormData();
      formData.append("agent_id", agentId);
      formData.append("query", userMessage.content);
      formData.append("target_lang", targetLang);

      const response = await fetch(`${API_BASE_URL}/agents/conversations`, {
        method: 'POST',
        body: formData,
      });

      if (!response.ok) throw new Error(`Network response was not ok (status: ${response.status})`);

      const data = await response.json();
      if (!data.answer) throw new Error("Invalid response from server");

      const streamedResponse = await streamResponse(data.answer);

      const botMessage = { role: 'assistant', content: streamedResponse, timestamp: new Date().toISOString() };
      setMessages((prev) => [...prev, botMessage]);
    } catch (error) {
      console.error("Fetch Error:", error);
      showSnackbar("Failed to send message", "error");
    } finally {
      setLoading(false);
      setStreamingText('');
      inputRef.current?.focus();
    }
  };

  // Message renderer with proper formatting
  const renderMessageContent = (content) => {
    // Split content by newlines and handle paragraphs properly
    const paragraphs = content.split('\n').filter(p => p.trim());
    
    if (paragraphs.length <= 1) {
      return <Typography variant="body1">{content}</Typography>;
    }
    
    return (
      <Stack spacing={1}>
        {paragraphs.map((paragraph, idx) => (
          <Typography key={idx} variant="body1">
            {paragraph}
          </Typography>
        ))}
      </Stack>
    );
  };

  return (
    <Box 
      sx={{ 
        display: 'flex', 
        flexDirection: 'column', 
        height: '100vh', 
        bgcolor: theme.palette.mode === 'dark' ? 'grey.900' : 'grey.50',
        position: 'relative',
        overflow: 'hidden'
      }}
    >
      {/* Custom styling for scrollbars */}
      <style>
        {`
          ::-webkit-scrollbar {
            width: 6px;
            height: 6px;
          }
          ::-webkit-scrollbar-track {
            background: transparent;
          }
          ::-webkit-scrollbar-thumb {
            background: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.2)' : 'rgba(0,0,0,0.2)'};
            border-radius: 3px;
          }
          ::-webkit-scrollbar-thumb:hover {
            background: ${theme.palette.mode === 'dark' ? 'rgba(255,255,255,0.3)' : 'rgba(0,0,0,0.3)'};
          }
        `}
      </style>
      
      {/* HEADER - Sticky at the top */}
      <AppBar 
        position="sticky" 
        elevation={0}
        sx={{ 
          top: 0, 
          left: 0, 
          width: '100%', 
          zIndex: 1100,
          background: theme.palette.mode === 'dark' 
            ? 'linear-gradient(to right, #1a237e, #283593)' 
            : 'linear-gradient(to right, #1976d2, #42a5f5)'
        }}
      >
        <Toolbar sx={{ minHeight: { xs: 64, sm: 70 } }}>
          <Badge
            overlap="circular"
            anchorOrigin={{ vertical: 'bottom', horizontal: 'right' }}
            badgeContent={
              <Box
                sx={{
                  width: 10,
                  height: 10,
                  borderRadius: '50%',
                  bgcolor: 'success.main',
                  border: '2px solid white',
                }}
              />
            }
          >
            <Avatar 
              sx={{ 
                bgcolor: 'primary.dark', 
                mr: 2,
                width: { xs: 38, sm: 42 },
                height: { xs: 38, sm: 42 },
                boxShadow: '0 2px 8px rgba(0,0,0,0.15)'
              }}
            >
              <BotIcon />
            </Avatar>
          </Badge>
          <Box>
            <Typography variant="h6" sx={{ fontWeight: 600 }}>{botName}</Typography>
            <Typography variant="caption" sx={{ opacity: 0.8 }}>
              {isLoading || isStreaming ? 'Typing...' : 'Online'}
            </Typography>
          </Box>
          <Box sx={{ flexGrow: 1 }} />
          <Tooltip title="Chat options">
            <IconButton color="inherit" edge="end">
              <MoreIcon />
            </IconButton>
          </Tooltip>
        </Toolbar>
      </AppBar>

      {/* CHAT MESSAGES - Scrollable */}
      <Box 
        sx={{ 
          flex: 1, 
          overflowY: 'auto', 
          p: { xs: 2, sm: 3 },
          backgroundImage: theme.palette.mode === 'dark' 
            ? 'radial-gradient(circle at center, rgba(25, 118, 210, 0.05) 0%, transparent 70%)' 
            : 'radial-gradient(circle at center, rgba(25, 118, 210, 0.03) 0%, transparent 70%)',
        }}
      >
        <Container 
          maxWidth="md" 
          sx={{ 
            height: '100%', 
            display: 'flex', 
            flexDirection: 'column',
            justifyContent: messages.length === 0 ? 'center' : 'flex-start'
          }}
        >
          {messages.length === 0 && !isLoadingInfo ? (
            <Box 
              sx={{ 
                textAlign: 'center', 
                p: 3, 
                opacity: 0.7,
                animation: 'fadeIn 0.5s ease-in-out'
              }}
            >
              <BotIcon sx={{ fontSize: 60, mb: 2, color: 'primary.main' }} />
              <Typography variant="h6">Start a conversation</Typography>
              <Typography variant="body2" sx={{ mt: 1 }}>
                Type a message to begin chatting with {botName}
              </Typography>
            </Box>
          ) : (
            <Stack spacing={3}>
              {messages.map((message, index) => (
                <Fade 
                  key={index}
                  in={true}
                  timeout={{ enter: 500 }}
                  style={{ transitionDelay: `${index * 50}ms` }}
                >
                  <Box>
                    <Box
                      sx={{
                        display: 'flex',
                        justifyContent: message.role === 'user' ? 'flex-end' : 'flex-start',
                        alignItems: 'flex-start',
                        gap: 1.5,
                        mb: 0.5
                      }}
                    >
                      {message.role === 'assistant' && (
                        <Avatar 
                          sx={{ 
                            bgcolor: 'primary.main', 
                            width: { xs: 32, sm: 36 }, 
                            height: { xs: 32, sm: 36 },
                            boxShadow: '0 2px 5px rgba(0,0,0,0.1)'
                          }}
                        >
                          <BotIcon sx={{ fontSize: { xs: 18, sm: 20 } }} />
                        </Avatar>
                      )}

                      <Paper
                        elevation={message.role === 'user' ? 1 : 2}
                        sx={{
                          p: { xs: 1.5, sm: 2 },
                          px: { xs: 2, sm: 2.5 },
                          borderRadius: 2.5,
                          maxWidth: { xs: '80%', sm: '70%', md: '65%' },
                          bgcolor: message.role === 'user' 
                            ? 'primary.main' 
                            : theme.palette.mode === 'dark' 
                              ? 'grey.800' 
                              : 'background.paper',
                          color: message.role === 'user' ? 'common.white' : 'text.primary',
                          boxShadow: message.role === 'user'
                            ? '0 2px 10px rgba(25, 118, 210, 0.3)'
                            : '0 2px 10px rgba(0, 0, 0, 0.05)',
                          position: 'relative',
                          '&::before': message.role === 'assistant' ? {
                            content: '""',
                            position: 'absolute',
                            top: 12,
                            left: -8,
                            width: 0,
                            height: 0,
                            borderTop: '8px solid transparent',
                            borderBottom: '8px solid transparent',
                            borderRight: `8px solid ${theme.palette.mode === 'dark' ? theme.palette.grey[800] : theme.palette.background.paper}`,
                          } : message.role === 'user' ? {
                            content: '""',
                            position: 'absolute',
                            top: 12,
                            right: -8,
                            width: 0,
                            height: 0,
                            borderTop: '8px solid transparent',
                            borderBottom: '8px solid transparent',
                            borderLeft: `8px solid ${theme.palette.primary.main}`,
                          } : {}
                        }}
                      >
                        {renderMessageContent(message.content)}
                      </Paper>

                      {message.role === 'user' && (
                        <Avatar 
                          sx={{ 
                            bgcolor: 'grey.300', 
                            width: { xs: 32, sm: 36 }, 
                            height: { xs: 32, sm: 36 },
                            boxShadow: '0 2px 5px rgba(0,0,0,0.1)'
                          }}
                        >
                          <PersonIcon sx={{ fontSize: { xs: 18, sm: 20 } }} />
                        </Avatar>
                      )}
                    </Box>
                    
                    {/* Message timestamp */}
                    <Box
                      sx={{
                        display: 'flex',
                        justifyContent: message.role === 'user' ? 'flex-end' : 'flex-start',
                        mt: 0.5,
                        ml: message.role === 'assistant' ? 7 : 0,
                        mr: message.role === 'user' ? 7 : 0,
                      }}
                    >
                      <Typography 
                        variant="caption" 
                        sx={{ 
                          color: 'text.secondary', 
                          opacity: 0.7, 
                          fontSize: '0.7rem',
                          display: 'flex',
                          alignItems: 'center',
                          gap: 0.5
                        }}
                      >
                        {formatMessageTime(message.timestamp)}
                        {message.role === 'user' && (
                          <CheckCircleIcon sx={{ fontSize: 12, color: 'success.main', ml: 0.5 }} />
                        )}
                      </Typography>
                    </Box>
                  </Box>
                </Fade>
              ))}

              {/* Streaming message preview */}
              {isStreaming && (
                <Fade in={true} timeout={300}>
                  <Box>
                    <Box sx={{ display: 'flex', gap: 1.5, alignItems: 'flex-start' }}>
                      <Avatar 
                        sx={{ 
                          bgcolor: 'primary.main', 
                          width: { xs: 32, sm: 36 }, 
                          height: { xs: 32, sm: 36 },
                          boxShadow: '0 2px 5px rgba(0,0,0,0.1)'
                        }}
                      >
                        <BotIcon sx={{ fontSize: { xs: 18, sm: 20 } }} />
                      </Avatar>
                      <Paper 
                        elevation={2} 
                        sx={{ 
                          p: { xs: 1.5, sm: 2 }, 
                          px: { xs: 2, sm: 2.5 },
                          borderRadius: 2.5, 
                          maxWidth: { xs: '80%', sm: '70%', md: '65%' },
                          bgcolor: theme.palette.mode === 'dark' ? 'grey.800' : 'background.paper',
                          boxShadow: '0 2px 10px rgba(0, 0, 0, 0.05)',
                          position: 'relative',
                          '&::before': {
                            content: '""',
                            position: 'absolute',
                            top: 12,
                            left: -8,
                            width: 0,
                            height: 0,
                            borderTop: '8px solid transparent',
                            borderBottom: '8px solid transparent',
                            borderRight: `8px solid ${theme.palette.mode === 'dark' ? theme.palette.grey[800] : theme.palette.background.paper}`,
                          }
                        }}
                      >
                        {renderMessageContent(streamingText || <TypingIndicator />)}
                      </Paper>
                    </Box>
                    <Box sx={{ display: 'flex', ml: 7, mt: 0.5 }}>
                      <Typography 
                        variant="caption" 
                        sx={{ color: 'text.secondary', opacity: 0.7, fontSize: '0.7rem' }}
                      >
                        Typing...
                      </Typography>
                    </Box>
                  </Box>
                </Fade>
              )}

              <div ref={messagesEndRef} />
            </Stack>
          )}
        </Container>
      </Box>

      {/* INPUT FIELD - Sticky at the bottom */}
      <Paper
        component="form"
        onSubmit={handleSubmit}
        elevation={3}
        sx={{
          p: { xs: 2, sm: 2.5 },
          borderTop: '1px solid',
          borderColor: 'divider',
          position: 'sticky',
          bottom: 0,
          left: 0,
          width: '100%',
          zIndex: 1100,
          bgcolor: theme.palette.mode === 'dark' ? 'grey.900' : 'background.paper',
        }}
      >
        <Container maxWidth="md">
          <Box sx={{ display: 'flex', gap: 1, alignItems: 'center' }}>
            {!isMobile && (
              <Tooltip title="Attach file">
                <IconButton size="medium" sx={{ color: 'text.secondary' }}>
                  <AttachFileIcon />
                </IconButton>
              </Tooltip>
            )}
            
            <TextField 
              fullWidth 
              variant="outlined"
              placeholder="Type your message..." 
              value={input} 
              onChange={(e) => setInput(e.target.value)}
              inputRef={inputRef}
              multiline
              maxRows={3}
              onKeyDown={(e) => {
                if (e.key === 'Enter' && !e.shiftKey) {
                  e.preventDefault();
                  handleSubmit(e);
                }
              }}
              InputProps={{
                sx: {
                  borderRadius: 3,
                  bgcolor: theme.palette.mode === 'dark' ? 'grey.800' : 'grey.100',
                  '&:hover': {
                    bgcolor: theme.palette.mode === 'dark' ? 'grey.700' : 'grey.200',
                  },
                  '& .MuiOutlinedInput-notchedOutline': {
                    borderColor: 'transparent',
                  },
                  '&.Mui-focused .MuiOutlinedInput-notchedOutline': {
                    borderColor: 'primary.main',
                    borderWidth: '1px',
                  },
                },
                endAdornment: (
                  <InputAdornment position="end">
                    <Tooltip title="Voice input">
                      <IconButton
                        size="small"
                        sx={{ mr: 0.5, color: 'text.secondary' }}
                      >
                        <MicIcon />
                      </IconButton>
                    </Tooltip>
                  </InputAdornment>
                ),
              }}
            />
            
            <IconButton 
              type="submit" 
              disabled={!input.trim() || isLoading}
              sx={{ 
                bgcolor: 'primary.main',
                color: 'white',
                p: { xs: 1, sm: 1.5 },
                '&:hover': {
                  bgcolor: 'primary.dark',
                },
                '&.Mui-disabled': {
                  bgcolor: 'action.disabledBackground',
                  color: 'action.disabled',
                }
              }}
            >
              {isLoading ? <CircularProgress size={24} color="inherit" /> : <SendIcon />}
            </IconButton>
          </Box>
          
          <Typography 
            variant="caption" 
            align="center" 
            sx={{ 
              display: 'block', 
              mt: 1, 
              color: 'text.secondary',
              opacity: 0.7
            }}
          >
            {botName} is powered by Teric Lab AI
          </Typography>
        </Container>
      </Paper>

      {/* Snackbar for notifications */}
      <Snackbar
        open={snackbar.open}
        autoHideDuration={6000}
        onClose={handleSnackbarClose}
        anchorOrigin={{ vertical: 'bottom', horizontal: 'center' }}
      >
        <Alert 
          onClose={handleSnackbarClose} 
          severity={snackbar.severity} 
          variant="filled"
          sx={{ width: '100%' }}
        >
          {snackbar.message}
        </Alert>
      </Snackbar>
    </Box>
  );
};

export default Chats;